          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Accept-Language': 'uz'
        }
      }
    )
      .then(res => {
        console.log(res.data)
        this.test_result = res.data
      })
  },
  watch: {
    page(page) {
      axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?page=${page}&&class_id=${this.tests_id}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language': 'uz'
          }
        }
      )
        .then(res => {
          console.log(res.data)
          this.test_result = res.data
        })
    }
  },
};
